

.navbar{
    width:100%;
    height:70px;
    border:1px solid black;
    background-color: rgb(3, 3, 45);
    color:white;
    display: flex;
    justify-content: space-between;
    padding:10px;
    align-items: center;
    
}


.navbar>.nav1{
    width:50%;
    height:90%;
    display: flex;
    align-items: center;
    justify-content: space-around;

}

.nav1>p:hover{
    background-color: azure;
    color:rgb(7, 7, 94);;
    border-radius:5px;
    padding:5px;

}
.img1{
    width:50px;
    height:50px;
    border-radius:50%;
    text-align: center;
}
.nav2{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width:20%;
    height:90%;

}
.nav1>p>a{
    color:white;
    text-decoration: none;
}
.nav1>p>a:hover{
    color:rgb(1, 1, 18);
}